import axios from 'axios'

const URL = "/api/notes"

const getNotes=()=>{
       return axios.get(URL).then(res=> { console.log(res.data)  
                                             return res.data})
}
const getNote=(id)=>{
    return axios.get(URL+`/${id}`).then(res=>{console.log(res.data)
      return res.data })
}
const addNote=(note)=>{
    console.log("The post tirggered from the frontend")
    return axios.post(URL,note).then(res=> {console.log("The backend data is ",res.data)
    return res.data} )
}

export {
    getNotes,
    addNote,
    getNote,
}