import React ,{useState,useEffect} from 'react';
import Note from './components/Note'
import TopBar from './components/TopBar'
import {getNotes,addNote} from './api/notes'

function App() {
  

  const [appData, setAppData]=useState([])
  const [newData,setNewData]=useState("")

  useEffect(() => {
     
    const loadData=async()=>{
      const data= await getNotes()
      console.log("The async data is ",data)
      setAppData(data)
     }
    loadData()
  }, [])

  const changeNoteImportance=(id)=>{
       
    setAppData( data =>  
        
       data.map( d => 
        Number(d.id)===id ?{...d ,important:!d.important}:d)
      )
  }

  const addNewData=()=>{
    console.log("New data is being added!!")
    const newNote={
        id:Math.max(...appData.map( note => note.id)) +1,
        content:newData,
        important:false,
        date:new Date().toDateString()
      }
      console.log("The id is ",newNote.id)
      setAppData( notes => [...notes,newNote])
      addNote(newNote)
      setNewData("")
  }

  if(!appData) return <div> Loading ....</div>
  return (
    <div>
        <TopBar newData={newData} addNewData={addNewData} changeNewData={(data)=>setNewData(data)}/>
        {appData.map( dataPoint => <Note key={dataPoint.id} {...dataPoint} changeImportance={changeNoteImportance}/>)}
    </div>
  )
}

export default App;
