import React from 'react'

export default function TopBar({changeNewData,addNewData,newData}) {

     const changeTextData=(event)=>{
      changeNewData(event.target.value)
     }

    return (
        <div>
             <label style={{padding:"20px"}}>Enter Value </label> 
              <input onChange={changeTextData}  value={newData} style={{padding:"20px"}} type="text" /> 
              <button style={{padding:"20px"}} onClick={addNewData}>Enter</button>
        </div>
    )
}
