
import React from 'react'

export default function Note({content,important,date,id,changeImportance}){
    const changeNoteImportance=()=>{
      changeImportance(id)
    }
    const style={display:"flex",
    alignItems:"center",
    justifyContent:"left",
    padding:"20px"}
    
    return (
        <div style={style}>   
                
                   <div style={{padding:"20px"}}>{id}</div>
                    <div style={{padding:"20px"}}> {content}</div> 
                    <input  onChange={changeNoteImportance}type="checkbox" checked ={important}style={{padding:"20px"}}/>
                    <div style={{padding:"20px"}}>{date}</div>
                         
          </div>
    )
}
